<template id="">
<div class="w-100 h-100 padd" style="">
	<div class="poleSettingTable" v-if="settingShow">
		<table class="w-100">
			<tr>
				<th @click="selectSettingCollumn = column"  v-for="(column, index) in settingGridColumns"  :key="column.name" :class="'position-relative text-center thSetting '+ (selectSettingCollumn.title==column.title?'select':'')" :colspan="column.childrens.length">
					<label class="my-0">
						<input type="checkbox" v-model="column.show">
						<span  class="pseudocheckbox"></span>
					</label>
					<span class="mintext">
						{{column.title}}
					</span>
					<div @click.stop="flipArray(index+1, index)" v-if="!(index==0)" class="but lef">
						<img src="@/assets/img/Arrow lef.png" alt="">
					</div>
					<div @click.stop="flipArray(index+1, index+2)" v-if="!(index== settingGridColumns.length-1)" class="but rig">
						<img src="@/assets/img/Arrow rig.png" alt="">
					</div>
				</th>
			</tr>
		</table>
		<table class="w-75 mx-auto">
			<tr>
				<th  v-for="(column, index) in get2rowSettingGridColumns"  :key="column.name" class="position-relative text-center thSetting" >
					<label class="my-0">
						<input type="checkbox" v-model="column.show">
						<span  class="pseudocheckbox"></span>
					</label>
					<span class="mintext">
						{{column.title}}
					</span>
					<div @click.stop="flipArray(index, index-1, true)" v-if="!(index==0)" class="but lef">
						<img src="@/assets/img/Arrow lef.png" alt="">
					</div>
					<div @click.stop="flipArray(index, index+1, true)" v-if="!(index== get2rowSettingGridColumns.length-1)" class="but rig">
						<img src="@/assets/img/Arrow rig.png" alt="">
					</div>
				</th>
			</tr>
		</table>
	</div>
	<div class=" overflow-auto h-100  sizeHeight pb-1" >


		<table id="overflow" class=" tableStyle"  >
			<!-- v-columns-resizable -->
			<thead id="">
				<tr><th v-for="column in getGridColumns"  :key="column.name" class="text-center" :colspan="getLength(column)">{{column.title}} </th></tr>

				<tr ><th @click="sortBy(child_col.name, child_col.sort)"  :key="child_col.name" class="link_cursor"  v-for="child_col in childrens_coolumns"   >
					{{child_col.title}}
					<img v-if="(columnSort==child_col.name&&keySort==1)" src="@/assets/img/play1.png" height="6px" alt="">
					<img v-if="(columnSort==child_col.name&&keySort==2)" src="@/assets/img/play2.png" height="6px" alt="">
				</th></tr>
			</thead>


			<tbody>
				<RowComponent :selectPlanets="selectPlanets" :selectRowTable="selectRowTable" v-for="(row, index) in sortData" :table_coolumns="childrens_coolumns" :name="row.name" :children="row.childrens" :ofset="0" :types="row.type" :key="row.name+index"></RowComponent>
			</tbody>
		</table>
	</div>

	<SummTableComponent :class="{bottomLip:true}" :childrens_row="childrens_row" :selectRowTable="selectRowTable"></SummTableComponent>
</div>
</template>

<script type="text/javascript">
import RowComponent from "./RowComponent.vue"
import SummTableComponent from '@/components/planets/compon/SummTableComponent.vue'
import ColumnResizer from '@/assets/js/ColumnResizer.js'

export default {
  components:{
    RowComponent,
		SummTableComponent,
  },
	props:{
		"gridData": Array,
		"settingShow": Boolean,
	},
	mounted(){
		if (localStorage.gridColumns) {
			this.gridColumns = JSON.parse(localStorage.getItem("gridColumns"))
		}
		let colResizable = ColumnResizer;
		let overflow = document.querySelector("#overflow");
		this.colResizable = new colResizable(overflow, {
				liveDrag:true,
				gripInnerHtml:"<div class='grip'></div>",
				draggingClass:"rangeDrag",
				resizeMode:'overflow'
		})

	},
	data() {
		return {
			colResizable: '',
      keySort: 1,
      columnSort: "",
			selectRowTable: {},
			lastSelectPlanet: false,
			lastSelectAct: false,  //false снятие выделения true выделение
			selectSettingCollumn:{},
			gridColumns: [
				{title: "№", setting:false, show:true, childrens:[
					{title: "г", width:"15px", name:"galaktick", sort:false, show:true, setting:false, },
					{title: "c", width:"15px", name:"sistem", sort:false, show:true, setting:false, },
				]},
				{title: "Планета", show:true, setting:true, childrens:[
					{title: "Название", width:"130px", name:"title", sort:true, show:true, setting:true, },
					{title: "Альянс", width:"120px", name:"alns", sort:true, show:true, setting:true, },
					{title: "Ранг", width:"60px", name:"rang", sort:true, show:true, setting:true, },
				]},
				{title: "Ресурсы", show:true, setting:true, childrens:[
					{title: "Ti", width:"60px", name:"resTi", sort:true, show:true, setting:true, },
					{title: "Si",  width:"60px", name:"resSi", sort:true, show:true, setting:true, },
					{title: "Am", width:"60px", name:"resAm", sort:true, show:true, setting:true, },
					{title: "Валюта", width:"80px", name:"resVal", sort:true, show:true, setting:true, },
					{title: "Шатлы", width:"250px", name:"resShatls", sort:true, show:true, setting:true, },
				]},
				{title: "Обломки", show:true, setting:true, childrens:[
					{title: "Ti", width:"60px", name:"obTi", sort:true, show:true, setting:true, },
					{title: "Si", width:"60px", name:"obSi", sort:true, show:true, setting:true, },
					{title: "Am", width:"60px", name:"obAm", sort:true, show:true, setting:true, },
				]},
				{title: "Боевые единицы", show:true, setting:true, childrens:[
					{title: "Оборона", width:"90px", name:"boOb", sort:true, show:true, setting:true, },
					{title: "Флот", width:"80px", name:"boFl", sort:true, show:true, setting:true, },
					{title: "ЗБ", width:"80px", name:"boaZb", sort:true, show:true, setting:true, },
					{title: "ТП", width:"80px", name:"boTp", sort:true, show:true, setting:true, },
				]},
				{title: "Стоимость", show:true, setting:true, childrens:[
					{title: "Оборона", width:"80px", name:"stOb", sort:true, show:true, setting:true, },
					{title: "Флот", width:"80px", name:"stfl", sort:true, show:true, setting:true, },
				]},
				{title: "Первый залп", show:true, setting:true, childrens:[
					{title: "Оборона", width:"80px", name:"perOb", sort:true, show:true, setting:true, },
					{title: "Флот", width:"80px", name:"perFl", sort:true, show:true, setting:true, },
				]},
				{title: "Уровни", show:true, setting:true, childrens:[
					{title: "Ti", width:"80px", name:"levTi", sort:true, show:true, setting:true, },
					{title: "Si", width:"80px", name:"levSi", sort:true, show:true, setting:true, },
					{title: "Am", width:"80px", name:"levAm", sort:true, show:true, setting:true, },
					{title: "Пс", width:"80px", name:"levPs", sort:true, show:true, setting:true, },
					{title: "Бд", width:"80px", name:"levBd", sort:true, show:true, setting:true, },
					{title: "Ад", width:"80px", name:"levAd", sort:true, show:true, setting:true, },
					{title: "Ппд", width:"80px", name:"levPpd", sort:true, show:true, setting:true, },
				]},
				{title: "Дополнительно", show:true, setting:true, childrens:[
					{title: "Карта", width:"80px", name:"dopMap", sort:true, show:true, setting:true, },
					{title: "Скан", width:"80px", name:"dopScan", sort:true, show:true, setting:true, },
					{title: "Атака", width:"80px", name:"dopAtaka", sort:true, show:true, setting:true, },
					{title: "Комментарий", width:"80px", name:"dopCom", sort:true, show:true, setting:true, },
				]},
			],
		}
	},
  computed:{
		settingGridColumns(){
			let childs = []
			this.gridColumns.forEach((item) => {
				if (item.setting) {
					childs.push(item)
				}
			localStorage.setItem("gridColumns", JSON.stringify(this.gridColumns))
			this.colResizable.applyBounds()

			})

			return childs
		},
		get2rowSettingGridColumns(){
			let childs = []
			if (!this.selectSettingCollumn.title) return []
			this.selectSettingCollumn.childrens.forEach((item) => {
				if (item.setting) {
					childs.push(item)
				}
			})
			return childs
		},
		getGridColumns(){
			let childs = []
			this.gridColumns.forEach((item) => {
				if (item.show) {
					childs.push(item)
				}
			})
			return childs
		},

    childrens_coolumns(){  //возвращает названия 2 ряда колонок
      let childs = []
			this.gridColumns.forEach((item) => {
				if (item.show) {
					childs = childs.concat(item.childrens.filter(function(child) {
						return child.show;
					}))
				}
			});
      return childs
    },

		childrens_row(){
			let childs = []
			this.gridData.forEach((item) => {
				childs = childs.concat(	this.getChild(item))
			});
			return childs
		},
    sortData() {
      return  this.sort_util(this.gridData)
    },

  },
  methods:{
		flipArray(index, nextindex, row2){ //Меняет местами два обьекта в массиве
			if (!row2) {
				this.gridColumns[index] = this.gridColumns.splice(nextindex,1, this.gridColumns[index])[0]
			}else {
				this.selectSettingCollumn.childrens[index] = this.selectSettingCollumn.childrens.splice(nextindex,1, this.selectSettingCollumn.childrens[index])[0]
			}
		},
		getChild(obj){ // рекрсивный метод для поиска дочерних строк в таблице
			let childs = []
			if (obj.type == "row") {
				return obj.childrens[0]
			}
			else {
				obj.childrens.forEach((item) => {
					childs = childs.concat(this.getChild(item))
				})
			}
			return childs;
		},
		selectPlanets(name, shift){ //Выделение ячеек
			if (shift && this.lastSelectPlanet) {
				let enter = 0   //0 - не выделяем 1 - начали выделять
				for (let i = 0; i < this.childrens_row.length; i++) {
					if ((this.childrens_row[i].title == this.lastSelectPlanet) || (this.childrens_row[i].title == name)) {
						if(enter == 0) enter = 1
						else {
							enter = 0
							this.lastSelectPlanet = false
							this.$set(this.selectRowTable, this.childrens_row[i].title, this.lastSelectAct)
						}
					}
					if (enter == 1) {
						this.$set(this.selectRowTable, this.childrens_row[i].title, this.lastSelectAct)
					}
				}
			}else if (shift && !this.lastSelectPlanet) {
				this.lastSelectPlanet = name
				this.$set(this.selectRowTable, name, !this.selectRowTable[name])
				this.lastSelectAct = this.selectRowTable[name]
			}
			else{
				this.lastSelectPlanet = false
				this.$set(this.selectRowTable, name, !this.selectRowTable[name])
				this.lastSelectAct = this.selectRowTable[name]
			}


		},
    sortBy(name, sort){ // сортировка таблицы
      if (!sort) return false

      if (this.keySort == 2 || this.columnSort!=name) {
        this.keySort = 1
      }else {
        this.keySort++
      }

      this.columnSort = name

    },
    sort_util(data){
      if (this.keySort == 0) {
        return data
      }
      if (data[0].childrens[0].type=="list") {
        data.forEach((item) => {
            this.sort_util(item.childrens)
        })
      }
      if (data[0].childrens[0].type=="row") {
        data.forEach((item) => {
          this.sort1(item.childrens)
        })
      }
      if (data[0].type=="row") {
          this.sort1(data)
      }

      return data
    },
    sort1(data){
      return data.sort((a,b)=>{
        if (a.childrens[0][this.columnSort] > b.childrens[0][this.columnSort]) {
          return (this.keySort==1)?1:-1;
        }
        if (a.childrens[0][this.columnSort] < b.childrens[0][this.columnSort]) {
          return (this.keySort==1)?-1:1;
        }
        return 0;
      })
    },
		getLength(column){
			return column.childrens.filter(function(child) {
				return child.show;
			}).length
		},
  }
}
</script>

<style media="screen" scoped="">
.tableStyle {
  /* table-layout: fixed; */
  width: 2000px;
	font-size: 13px;
	color: #e6e6e6;
}
/deep/ .border-none{
  border: none!important;
}
.link_cursor{
  cursor:pointer;
}
/deep/th {
	border: 1px solid #606060;
  font-weight: 400;
	padding: 0 10px!important;
	padding-bottom: 5px;
}
/deep/ td {
	user-select: none; /* CSS3 (little to no support) */ /*Отключает выделение*/
	-ms-user-select: none; /* IE 10+ */
	-moz-user-select: none; /* Gecko (Firefox) */
	-webkit-user-select: none; /* Webkit (Safari, Chrome) */

	border: 1px solid #606060;
  font-size: 12px;
	padding: 0 10px!important;
	padding-bottom: 5px;
}
.padd{
	padding-top: 30px;
}
.pb50{
	padding-bottom: 50px;
}
.poleSettingTable{
	z-index: 10;
	width: 100%;
	color: #DEDEDE;
	background: rgba(55, 51, 73, 0.6);
	min-height: 52px;
}
.bottomLip{
	bottom: 0px!important;
}
input[type=checkbox] { display: none; }

.pseudocheckbox::before {
    content: "\00A0";
    display: inline-block;
    box-sizing: border-box;
    width: 17px;
    height: 17px;
    background-color: #dad9da;
    border: 2px solid rgb(60 59 75);
    border-radius: 0px;
    vertical-align: baseline;
    text-align: center;
    font-size: 14px;
    line-height: 14px;
    font-weight: bold;
    color: #3c3b4a;
		margin-bottom: 0;
		cursor: pointer;
}

input[type=checkbox]:checked + .pseudocheckbox::before { content: "\2713"; }
.thSetting{
	cursor: pointer;
}
.sizeHeight{
	height: calc(100% - 40px)!important;
	width: 100%
}
.thSetting.select{
	background: rgba(0, 0, 0, 0.35);
}
.lef{
	left: 0;
}
.rig{
	right: 0;
}
.but{
	height: 100%;
	padding: 0px 10px;
	background: #373349;
	top: 0px;
	position: absolute;
	display: none;
	z-index: 99;
}
.thSetting:hover>.but{
	display: block;
}
.disnone{
	display: none!important;
}
</style>
