<template id="">
  <div class="summ_fixed ">
   <table class="w-100"   >
     <tbody>
       <tr class="">
         <td width="150px">Всего выбрано: <span class="yellow_main">{{countRow.planetCount}}</span></td>
         <td :key="child_col.name" class=""  v-for="child_col in childrens_coolumns_summ"   >
           {{child_col.title}}: <span class="yellow_main">{{countRow[child_col.name]?countRow[child_col.name]:"0"}}</span>
         </td>
       </tr>
       <tr><td v-for="column in sumTable"  :key="column.name" class="text-center" :colspan="column.childrens.length">{{column.title}} </td></tr>
     </tbody>
   </table>
 </div>
</template>

<script type="text/javascript">
export default {
  props:[
    "childrens_row",
    "selectRowTable",

  ],
  mounted(){
    // this.scrollLeft =  document.getElementById('fonPlanet').scrollLeft
  },
  data(){
    return {
      sumTable: [
				{title:"Планеты", childrens:[
					{title: "Количество планет", width:"15px", name:"planetCount" },
				]},
				{title: "Ресурсы", childrens:[
          {title: "Ti", width:"60px", name:"resTi", sort:true, summ:true },
          {title: "Si",  width:"60px", name:"resSi", sort:true, summ:true },
          {title: "Am", width:"60px", name:"resAm", sort:true, summ:true },
          {title: "Валюта", width:"80px", name:"resVal", sort:true, summ:true },
          {title: "Шатлы", width:"250px", name:"resShatls", sort:true, summ:true },
        ]},
        {title: "Обломки", childrens:[
          {title: "Ti", width:"60px", name:"obTi", sort:true, summ:true },
          {title: "Si", width:"60px", name:"obSi", sort:true, summ:true },
          {title: "Am", width:"60px", name:"obAm", sort:true, summ:true },
        ]},
			],
    }
  },
  computed:{
    scrollLeft(){
      return document.getElementById('fonPlanet').scrollLeft
    },
    childrens_coolumns_summ(){  //возвращает названия колонок для суммы
      let childs = []
      this.sumTable.forEach((item) => {
        item.childrens.forEach((i) => {
          if (i.summ) {
            childs.push(i)
          }
        })
      });
      return childs
    },
    countRow(){
			let sumObj = {}, planetCount = 0
			this.childrens_row.forEach((item) => {
				if (this.selectRowTable[item.title]) {
					planetCount++
					this.childrens_coolumns_summ.forEach((utem) => {
						if (isNaN(sumObj[utem.name])) {
							sumObj[utem.name] = 0 +  Number(item[utem.name])
						}else {
							sumObj[utem.name] = Number(sumObj[utem.name]) +  Number(item[utem.name])
						}
					})
				}
			})
			sumObj.planetCount = planetCount
			return sumObj
		}
  }
}
</script>

<style media="screen" scoped="">
.summ_fixed{
	position: fixed;
  width: 100%;
	bottom: 15px;
	color: #e6e6e6;
	left: 0;
	right: 0;
	background: #2F3136;
}
</style>
